// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
/*css*/
import '../static/css/comm.css'
import '../static/css/swiper-3.4.2.min.css'
import '../static/css/idangerous.swiper2.7.6.css'
import '../static/css/idangerous.swiper.3dflow.css'

/*js*/
import $ from 'jquery'
import niceScroll from 'nicescroll'
import axios from 'axios'

/*字体*/
import '../static/fonts/7-stroke/7-stroke.css'
import '../static/fonts/font-awesome/font-awesome.css'
import '../static/fonts/glyphicons/glyphicons.css'
import '../static/fonts/ionicons/ionicons.css'
import '../static/fonts/material-design/material-design.css'
import '../static/fonts/mfglabs/mfglabs.css'
import '../static/fonts/octicons/octicons.css'
import '../static/fonts/open-iconic/open-iconic.css'
import '../static/fonts/themify/themify.css'
import '../static/fonts/weather-icons/weather-icons.css'
import '../static/fonts/web-icons/web-icons.css'


Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.use(axios);
// jQuery.default.withCredentials = true;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
});



