<template>
  <el-container class="login-bg">
    <el-main style="min-height: calc(100vh)">
      <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="24">
          <div class="user-info">
            <div class="login-method">
              <div class="logo"></div>
              <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="first">
                  <span slot="label"><i class="login-icon face-recognition"></i> 人脸识别</span>
                  <div class="login-content">
                    <div class="canvas">
                      <div class="canvas-content">
                        <video ref="video" id="video" style="z-index: 1" width="316" height="266"></video>
                        <canvas ref="canvas" v-show="isShowImg" id="canvas" style="z-index: 2"></canvas>
                      </div>
                    </div>
                    <div class="submit-div" @click="photo">
                      <span><i class="login-icon take-photo"></i> 拍摄</span>
                    </div>
                  </div>
                </el-tab-pane>
                <el-tab-pane name="second">
                  <span slot="label"><i class="login-icon user-recognition"></i> 帐号登录</span>
                  <div class="form">
                    <div>
                      <span class="user"></span>
                      <input type="text"/>
                    </div>
                    <div>
                      <span class="pwd"></span>
                      <input type="password"/>
                    </div>
                    <span class="submit-span">登录 <i></i></span>
                  </div>
                </el-tab-pane>
              </el-tabs>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  import comm from './Common.vue'
  export default {
    name: "login",
    data() {
      return {
        activeName: 'first',
        form: {
          username: "",
          password: "",
        },
        isShowImg: false,
        dataURL: ""
      }
    },
    mounted() {
      this.initPhoto()
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      initPhoto() {
        let vm = this;
        // 浏览器兼容
        let mediaDevices = navigator.mediaDevices.getUserMedia({audio: false, video: {width: 316, height: 266}});
        mediaDevices
          .then(mediaStream => {
            let video = vm.$refs.video;
            video.src = window.URL.createObjectURL(mediaStream);
            video.onloadedmetadata = (e) => {
              video.play()
            };
            vm.video = video;
            vm.track = mediaStream.getTracks()[0]
          })
          .catch(err => {
            console.log('err.message' + err.name);
            vm.$message.error('未检测到摄像头!')
          })
      },
      //拍照
      photo() {
        let vm = this;
        let dataURL = null;
        let canvas = vm.$refs.canvas;
        let context = canvas.getContext('2d');
        let width = 316;
        let height = 266;
        canvas.width = width;
        canvas.height = height;
        context.drawImage(vm.video, 0, 0, width, height);
        dataURL = canvas.toDataURL('image/png');
        vm.dataURL = dataURL;
        vm.isShowImg = true;
        $.ajax({
          type:"post",
          url:comm.Url+"/image/compare",
          data:{
            imageUrl:dataURL
          },
          // headers: {
          //   'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          // },
          // xhrFields: {
          //   withCredentials: true
          // },
          // beforeSend: function(request) {
          //   request.setRequestHeader("Content-Type", "application/x-www-form-urlencoded; charset=UTF-8");
          // },
          // crossDomain: true,
          success:function (result) {
            console.log(result)
          }
        });
        setTimeout(() => {
          vm.isShowImg = false;
        }, 5000)
      },
    }

  }

</script>

<style scoped>
  .login-bg {
    background: url("../assets/img/login.png") no-repeat;
    background-size: 100%;
  }

  .user-info {
    position: fixed;
    top: 50%;
    left: 50%;
    background: url("../assets/img/login_bg.png") no-repeat 50% 50%;
    width: 726px;
    height: 600px;
    background-size: 600px;
    -webkit-transform: translateX(-50%) translateY(-50%);
  }

  .login-method {
    position: absolute;
    width: 356px;
    padding: 0 20px;
    left: 165px;
  }

  .logo {
    height: 68px;
    background: url("../assets/img/logo1.png") no-repeat 50% 0;
    background-size: 300px;
    margin-bottom: 10px;
  }

  .login-icon {
    display: inline-block;
    width: 16px;
    height: 14px;
    position: relative;
    top: 2px;
  }

  .face-recognition {
    background: url("../assets/img/scan.png");
  }

  .user-recognition {
    background: url("../assets/img/user.png");
  }

  .login-content {
    height: 369px;
  }

  .canvas {
    height: 266px;
    padding: 17px 19px;
    background: url("../assets/img/kuangkuang.png") no-repeat 50% 50%;
    background-size: cover;
    margin-bottom: 20px;
  }

  .canvas-content {
    height: 100%;
    width: 100%;
    position: relative;
  }

  .take-photo {
    background: url("../assets/img/photo.png") no-repeat;
    background-size: contain;
  }

  .submit-div {
    margin: 0 10px;
    height: 45px;
    border: 1px #fff solid;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.6);
    background-color: rgba(0, 0, 0, 0.21);
    border-radius: 5px;
    text-align: center;
    line-height: 45px;
    color: #fff;
    cursor: pointer;
  }

  .form {
    padding: 20px 35px;
    height: 300px;
    position: relative;
  }

  .form > div:nth-child(1) {
    margin-top: 80px;
    margin-bottom: 50px;
  }

  .form span {
    display: inline-block;
    width: 28px;
    height: 28px;
  }

  .form input {
    background: none;
    outline: none;
    border: none;
    border-bottom: 1px solid #fff;
    width: 80%;
    height: 26px;
    color: #fff;
    margin-left: 10px;
  }

  .user {
    background: url("../assets/img/man.png") no-repeat 50% 50%;
    position: relative;
    top: 8px;
  }

  .pwd {
    background: url("../assets/img/psw.png") no-repeat 50% 50%;
    position: relative;
    top: 8px;
  }

  .submit-span {
    position: absolute;
    bottom: 45px;
    width: 54px !important;
    color: #fff;
    cursor: pointer;
    right: 35px;
    font-size: 15px;
  }

  .submit-span i {
    display: inline-block;
    width: 13px;
    height: 13px;
    background: url("../assets/img/right.png") no-repeat 50% 50%;
    position: relative;
    top: 1px;
  }

  #canvas {
    position: absolute;
    top: 0;
  }
</style>



